import TeacherHeader from './components/header'
import TeacherList from './components/list'
import axios from 'axios'

export default {
  name: 'assistant',
  components: {
    TeacherHeader,
    TeacherList
  },
  data () {
    return {
      projectList: []
    }
  },
  methods: {
    getProjectList () {
      let level = sessionStorage.getItem('level')
      if (level > 4) {
        axios.get(this.url + '/project/all')
          .then(this.getProjectListSucc)
      } else if (level === 4) {
        axios.get(this.url + '/project/leader/' + sessionStorage.getItem('id'))
          .then(this.getProjectListSucc)
        axios.get(this.url + '/project/participant/' + sessionStorage.getItem('id'))
          .then(this.getProjectListSucc)
      } else {
        axios.get(this.url + '/project/participant/' + sessionStorage.getItem('id'))
          .then(this.getProjectListSucc)
      }
    },
    getProjectListSucc (res) {
      console.log(res.status)
      res = res.data
      if (res) {
        this.projectList = this.projectList.concat(res)
        console.log(this.projectList)
      }
    }
  },
  mounted () {
    this.getProjectList()
  }
}



